












































































import {Component, Vue} from 'vue-property-decorator'
import {deleteTableItem, getTableData} from "@/api/user";

@Component
export default class Table extends Vue {
    input21: any = ''
    dialogStatus: string = ''

    textMap: object = {
        update: 'Edit',
        create: 'Create'
    }
    dialogFormVisible: boolean = false

    //table数据
    formData: any = []
    tableLoading: boolean = false

    //编辑界面的数据
    editForm: any = {
        id: '0',
        name: '',
        sex: 1,
        age: 0,
        birth: '',
        addr: ''
    }

    //编辑界面校验
    ruleForms: object = {
        name: [{required: true, message: '请输入姓名', trigger: 'blur'}],
        sex: [{required: true, message: '请输入性别', trigger: 'blur'}],
        age: [{required: true, message: '请输入年龄', trigger: 'blur'}],
        birth: [{required: true, message: '请输入生日', trigger: 'blur'}],
        addr: [{required: true, message: '请输入居住地址', trigger: 'blur'}]
    }

    //分页数据
    pageSize: number = 10
    pageNum: number = 1
    total: number = 0

    mounted() {
        this.initTables()
    }

    //初始化表格数据
    initTables(): void {
        this.tableLoading = true
        getTableData({
            pageNum: this.pageNum
        }).then((res: any) => {
            this.tableLoading = false;
            if (res.code === 200) {
                this.formData = res.data.list
                this.total = res.data.total
            } else {
                this.$message.error(res.msg)
            }
        })
    }

    // 显示编辑界面
    handleEdit(id: number): void {
        this.dialogStatus = 'update'
        this.dialogFormVisible = true
    }

    // 显示新增界面
    handleAdd(): void {
        this.dialogStatus = 'create'
        this.dialogFormVisible = true
    }

    //新增数据
    createData(): void {
        this.dialogFormVisible = false
    }

    // 编辑
    updateData(): void {
        this.dialogFormVisible = false
    }

    //删除表单数据
    deleteData(id: number): void {
        this.$confirm('确认删除这条记录么？', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
        }).then(() => {
            deleteTableItem(id).then((res: any) => {
                if (res.code === 200) {
                    this.$message.success('删除成功')
                    this.initTables()
                } else {
                    this.$message.error(res.msg)
                }
            })
        }).catch(() => {
        })
    }

    //分页显示
    handleCurrentChange(val: number): void {
        this.pageNum = val
        this.initTables()
    }

    //改变行的背景颜色
    tableRowClassName(row: number, rowIndex: number): String {
        let odd, even
        if (rowIndex % 2 == 0) {
            return 'warning-row';
        } else {
            return 'success-row'
        }
    }
}
